// components/BillVisualization.tsx
'use client';
import { useState } from 'react';
import { Bar } from 'react-chartjs-2';
import { PieChart, Pie, Cell, Tooltip, Legend, ResponsiveContainer } from 'recharts';
import { ParentSize } from '@visx/responsive';
import { LineChart, Line, Axis, Grid, Tooltip as VisxTooltip, XYChart } from '@visx/xychart';
import { curveMonotoneX } from '@visx/curve';
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  BarElement,
  Tooltip as ChartJSTooltip,
  Legend as ChartJSLegend,
} from 'chart.js';

// Register ChartJS components
ChartJS.register(
  CategoryScale,
  LinearScale,
  BarElement,
  ChartJSTooltip,
  ChartJSLegend
);

type BillItem = {
  id: string;
  description: string;
  amount: number;
  category: string;
};

type BillVisualizationProps = {
  bill: {
    lineItems: BillItem[];
    total: number;
    date: string;
  };
  historicalData?: {
    month: string;
    current: number;
    previous: number;
    average: number;
  }[];
};

const COLORS = ['#0088FE', '#00C49F', '#FFBB28', '#FF8042', '#8884D8'];

export default function BillVisualization({ bill, historicalData }: BillVisualizationProps) {
  const [visualizationType, setVisualizationType] = useState<'bar' | 'pie' | 'trend'>('bar');
 // Add to your component
useEffect(() => {
  const savedType = localStorage.getItem('preferredVizType');
  if (savedType) setVisualizationType(savedType as any);
}, []);

useEffect(() => {
  localStorage.setItem('preferredVizType', visualizationType);
}, [visualizationType]);
  // Prepare data for visualizations
  const barChartData = {
    labels: bill.lineItems.map(item => item.description),
    datasets: [{
      label: 'Amount ($)',
      data: bill.lineItems.map(item => item.amount),
      backgroundColor: COLORS,
      borderColor: COLORS.map(color => color.replace('0.6', '1')),
      borderWidth: 1,
    }],
  };

  const pieChartData = bill.lineItems.reduce((acc, item) => {
    const existing = acc.find(i => i.name === item.category);
    if (existing) {
      existing.value += item.amount;
    } else {
      acc.push({ name: item.category, value: item.amount });
    }
    return acc;
  }, [] as { name: string; value: number }[]);

  const barChartOptions = {
    responsive: true,
    plugins: {
      tooltip: {
        callbacks: {
          label: (context: any) => `$${context.raw.toFixed(2)}`,
        },
      },
    },
  };

  return (
    <div className="bg-white p-4 rounded-lg shadow">
      <div className="flex justify-between items-center mb-4">
        <h3 className="text-lg font-semibold">Bill Visualization</h3>
        <div className="relative">
          <select
            value={visualizationType}
            onChange={(e) => setVisualizationType(e.target.value as any)}
            className="appearance-none bg-gray-50 border border-gray-300 text-gray-700 py-2 px-3 pr-8 rounded leading-tight focus:outline-none focus:ring-2 focus:ring-blue-500"
          >
            <option value="bar">Bar Chart</option>
            <option value="pie">Pie Chart</option>
            {historicalData && <option value="trend">Trend Analysis</option>}
          </select>
          <div className="pointer-events-none absolute inset-y-0 right-0 flex items-center px-2 text-gray-700">
            <svg className="fill-current h-4 w-4" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20">
              <path d="M9.293 12.95l.707.707L15.657 8l-1.414-1.414L10 10.828 5.757 6.586 4.343 8z" />
            </svg>
          </div>
        </div>
      </div>

      <div className="h-64">
        {visualizationType === 'bar' && (
          <Bar data={barChartData} options={barChartOptions} />
        )}

        {visualizationType === 'pie' && (
          <ResponsiveContainer width="100%" height="100%">
            <PieChart>
              <Pie
                data={pieChartData}
                cx="50%"
                cy="50%"
                labelLine={false}
                outerRadius={80}
                fill="#8884d8"
                dataKey="value"
                nameKey="name"
                label={({ name, percent }) => `${name} (${(percent * 100).toFixed(0)}%)`}
              >
                {pieChartData.map((entry, index) => (
                  <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />
                ))}
              </Pie>
              <Tooltip 
                formatter={(value: number) => [`$${value.toFixed(2)}`, 'Amount']}
                labelFormatter={(label) => `Category: ${label}`}
              />
              <Legend />
            </PieChart>
          </ResponsiveContainer>
        )}

        {visualizationType === 'trend' && historicalData && (
          <ParentSize>
            {({ width, height }) => (
              <XYChart
                width={width}
                height={height}
                xScale={{ type: 'band' }}
                yScale={{ type: 'linear' }}
                margin={{ top: 20, right: 20, bottom: 40, left: 40 }}
              >
                <Grid columns={false} numTicks={4} strokeDasharray="4" stroke="#eee" />
                <Axis orientation="bottom" tickFormat={(d) => d.slice(0, 3)} />
                <Axis orientation="left" tickFormat={(d) => `$${d}`} />
                <Line
                  dataKey="Current"
                  data={historicalData}
                  xAccessor={(d) => d.month}
                  yAccessor={(d) => d.current}
                  stroke="#4f46e5"
                  strokeWidth={2}
                  curve={curveMonotoneX}
                />
                <Line
                  dataKey="Average"
                  data={historicalData}
                  xAccessor={(d) => d.month}
                  yAccessor={(d) => d.average}
                  stroke="#6b7280"
                  strokeWidth={1.5}
                  strokeDasharray="4"
                />
                <VisxTooltip<typeof historicalData[0]>
                  snapTooltipToDatumX
                  snapTooltipToDatumY
                  showVerticalCrosshair
                  showSeriesGlyphs
                  renderTooltip={({ tooltipData }) => (
                    <div className="bg-white p-2 border border-gray-200 rounded shadow">
                      <div className="font-semibold">{tooltipData?.nearestDatum?.key}</div>
                      <div>Month: {tooltipData?.nearestDatum?.datum.month}</div>
                      <div>Amount: ${tooltipData?.nearestDatum?.datum.current.toFixed(2)}</div>
                    </div>
                  )}
                />
              </XYChart>
            )}
          </ParentSize>
        )}
      </div>

      <div className="mt-2 text-sm text-gray-500">
        {visualizationType === 'bar' && 'Click bars for details'}
        {visualizationType === 'pie' && 'Hover segments for amounts'}
        {visualizationType === 'trend' && 'Comparing current bill to historical data'}
      </div>
    </div>
  );
}
